/* Container */
    #Dish {
        display: flex;
        align-content: center;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
        position: absolute;
        left: 0px;
        bottom: 0px;
        top: 0px;
        right: 0px;
    }

    #myDish {
        display: flex;
        align-content: center;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
        position: absolute;
        left: 0px;
        bottom: 0px;
        top: 0px;
        right: 0px;
    }
/* Camera */
    #Dish div {

        position: relative;
        vertical-align: middle;
        align-self: center;
        border-radius: 10px;
        overflow: hidden;
        display: inline-block;
        background: #fff;
        animation: show 0.4s ease;

    }

    #myDish div {

        position: relative;
        vertical-align: middle;
        align-self: center;
        border-radius: 10px;
        overflow: hidden;
        display: inline-block;
        background: #fff;
        animation: show 0.4s ease;

    }
/* Details (optional) */
    /* Aspect Ratio Number */
        #Dish div:after {
            color: #aaa;
            font-size: 15px;
            font-family: "iransans" !important;
            position: absolute;
            bottom: 20px;
            right: 23px;
            font-weight: 200;
            content: attr(data-content);
            display: block;
        }

        #myDish div:after {
            color: #aaa;
            font-size: 15px;
            font-family: "iransans" !important;
            position: absolute;
            bottom: 20px;
            right: 23px;
            font-weight: 200;
            content: attr(data-content);
            display: block;
        }
    /* Gray Diagonal */

/* Animation */
    @keyframes show {
        0% {
            opacity: 0;
            transform: scale(0.4) translateY(20px);
        }
        100% {
            opacity: 1;
            transform: scale(1) translateY(0);
        }
    }